
.detail-wrapper {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #222;
}
.detail-wrapper .back {
  position: absolute;
  top: 0;
  left: 6px;
  z-index: 50;
}
.detail-wrapper .back .icon-back {
  display: block;
  padding: 10px;
  font-size: 22px;
  color: #ffcd32;
}
.detail-wrapper .title {
  position: absolute;
  top: 0;
  left: 10%;
  z-index: 40;
  width: 80%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
  line-height: 40px;
  font-size: 18px;
  color: #fff;
}
.detail-wrapper .detail-scroll {
  position: absolute;
  overflow: hidden;
  top: 40px;
  bottom: 0;
  left: 0;
  right: 0;
}
.detail-wrapper .detail-scroll .scroll-wrapper {
  padding: 0 8px 0 8px;
}
.detail-wrapper .detail-scroll .scroll-wrapper .description p {
  margin-top: 2px;
  margin-bottom: 6px;
  display: block;
  font-size: 16px;
  line-height: 16px;
  font-weight: 200;
  text-indent: 8px;
}
.detail-wrapper .detail-scroll .scroll-wrapper .description p:nth-child(2) {
  margin-top: 0;
  color: #ffeb3b;
}
.detail-wrapper .detail-scroll .scroll-wrapper .situation {
  margin: 10px auto 0 auto;
  text-align: center;
}
.slide-enter-active,
.slide-leave-active {
  transition: all 0.3s;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}


